* {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}
body {
    background-color: #1c1b1c;
    background-image: linear-gradient(to bottom, #212021, #1c1b1c);
    overflow-x: hidden;
}
.App {
    display: flex;
    height: 100vh;
}
.preview {
    width: 15vw;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    transition: 0.5s;
}
.preview:hover {
    scale: 1.1;
    transform: translateY(-5px) translateX(5px);
    z-index: 1000;
}
.Menu {
    overflow: hidden;
    padding: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    gap: 10px;
}
svg {
    display: none;
}

body {
    background-image: unset;   
}

@media (max-width: 1225px) {
    .App {
        height: unset;
    }
    .Menu {
        overflow: unset;
        flex-direction: column;
        gap: 30px;
        position: relative;
    }
    .preview {
        width: 60vw;
    }
    svg {
        display: block;
        position: absolute;
        top: 700px;
        width: 8vw;
        right: -80px;
    }
}

@media (max-width: 425px) {
    svg {
        display: block;
        position: absolute;
        top: 700px;
        width: 10vw;
        right: -40px;
    }
}